.NONE {
  display: none;
}

.HIDDEN {
  visibility: hidden;
}

.Revival {
  visibility: visible;
}

.Link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Z-index {
  z-index: -999;
}

.height101 {
  height: 101%;
}

.sticky {
  position: sticky;
}

.transform_Y-60px {
  transform: translate(0, -60px);
}